import Prism from 'prismjs'; // Імпортуємо Prism.js
import 'prismjs/themes/prism.css'; // Імпортуємо стиль для Prism.js

import 'prismjs/components/prism-python.js';
import 'prismjs/components/prism-javascript.js';
import 'prismjs/components/prism-java.js';
import 'prismjs/components/prism-go.js';
import 'prismjs/components/prism-ruby.js';
import 'prismjs/components/prism-rust.js';
import 'prismjs/components/prism-kotlin.js';
import 'prismjs/components/prism-dart.js';
import 'prismjs/components/prism-matlab.js';
import 'prismjs/components/prism-json.js';
import 'prismjs/components/prism-http.js';


import * as curlconverter from 'curlconverter';

// DOM-елементи
const input = document.getElementById('cURL-input');
const output = document.getElementById('output');
const copyButton = document.getElementById('copy-btn');

// Об'єкт мов і функцій конвертації
const languages = {
  python: curlconverter.toPython,
  javascript: curlconverter.toJavaScript,
  nodejs: curlconverter.toNode,
  nodeaxios: curlconverter.toNodeAxios,
  php: curlconverter.toPhp,
  java: curlconverter.toJava,
  go: curlconverter.toGo,
  ruby: curlconverter.toRuby,
  rust: curlconverter.toRust,
  powershell: curlconverter.toPowershellWebRequest,
  r: curlconverter.toR,
  kotlin: curlconverter.toKotlin,
  dart: curlconverter.toDart,
  matlab: curlconverter.toMATLAB,
  json: curlconverter.toJsonString,
  http: curlconverter.toHTTP,
  cfml: curlconverter.toCFML,
};

// Відповідність мов Prism.js
const prismLanguages = {
  python: 'python',
  javascript: 'javascript',
  nodejs: 'javascript',
  nodeaxios: 'javascript',
  php: 'ruby',
  java: 'java',
  go: 'go',
  ruby: 'ruby',
  rust: 'rust',
  kotlin: 'kotlin',
  dart: 'dart',
  matlab: 'matlab',
  json: 'json',
  http: 'http',
  cfml: 'http',
};

// Отримання поточної мови
const getCurrentLanguage = () => {
  const currentLanguage = output.dataset.outputLanguage || 'python';
  console.log(`Current selected language: ${currentLanguage}`);
  return currentLanguage;
};

// Оновлення виводу
const updateOutput = (curlCommand) => {
  const selectedLanguage = getCurrentLanguage();
  const converter = languages[selectedLanguage];

  if (!converter) {
    console.error(`No converter found for language "${selectedLanguage}"`);
    output.textContent = `Language "${selectedLanguage}" is not supported.`;
    return;
  }

  console.log(`Converting to: ${selectedLanguage}`);

  try {
    const code = converter(curlCommand); // Конвертуємо cURL
    output.textContent = code; // Додаємо текст у `output`
    const prismLanguage = prismLanguages[selectedLanguage] || 'plaintext'; // Вибір мови для Prism.js
    output.className = `language-${prismLanguage}`; // Встановлення класу для Prism.js
    Prism.highlightElement(output); // Підсвічуємо код
  } catch (error) {
    console.error('Error during conversion:', error);
    output.textContent = 'Invalid cURL command.';
  }
};

// Слухач змін у текстовому полі
input.addEventListener('input', () => {
  updateOutput(input.value);
});

// Слухач для кнопки "Copy"
copyButton.addEventListener('click', () => {
  navigator.clipboard.writeText(output.textContent).then(() => {
    alert('Code copied to clipboard!');
  }).catch((err) => {
    console.error('Failed to copy text:', err);
  });
});

// Спостереження за зміною атрибута `data-output-language`
const observer = new MutationObserver(() => {
  console.log(`Language changed to: ${getCurrentLanguage()}`);
  updateOutput(input.value); // Оновлюємо вивід, якщо змінилась мова
});

// Запускаємо спостерігач за атрибутом `data-output-language`
observer.observe(output, { attributes: true, attributeFilter: ['data-output-language'] });

// Початкова ініціалізація
updateOutput(input.value);
